import { useState, useEffect } from "react";
import axios from "axios";

export default function ShippingManagement() {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(false);
  const [newOrder, setNewOrder] = useState({});

  // Fetch orders (mock or API integration)
  useEffect(() => {
    const fetchOrders = async () => {
      setLoading(true);
      try {
        // Static mock data for orders
        const mockOrders = [
          {
            id: "1",
            customer_name: "John Doe",
            address: "123 Main Street, Springfield",
            status: "Processing",
            shipment_id: "1736354081235",
          },
          {
            id: "2",
            customer_name: "Jane Smith",
            address: "456 Elm Street, Shelbyville",
            status: "Shipped",
            shipment_id: "SR67890",
          },
          {
            id: "3",
            customer_name: "Alice Brown",
            address: "789 Oak Avenue, Capital City",
            status: "Delivered",
            shipment_id: "SR11223",
          },
        ];
  
        // Simulate API response delay
        await new Promise((resolve) => setTimeout(resolve, 1000));
        setOrders(mockOrders);
      } catch (error) {
        console.error("Error fetching orders:", error);
      } finally {
        setLoading(false);
      }
    };
  
    fetchOrders();
  }, []);
  

  // Create Shipping Order
  const createShippingOrder = async () => {
    try {
      const response = await axios.post("/api/orders", {
        action: "create",
        data: newOrder,
      });
      alert("Shipping order created successfully!");
      setOrders([...orders, response.data]);
    } catch (error) {
      console.error("Error creating order:", error);
      alert("Failed to create shipping order.");
    }
  };

  // Cancel Shipping Order
  const cancelOrder = async (shipmentId) => {
    try {
      await axios.post("/api/orders", { action: "cancel", data: { shipmentId } });
      alert("Order canceled successfully.");
      setOrders(orders.filter((order) => order.shipment_id !== shipmentId));
    } catch (error) {
      console.error("Error canceling order:", error);
      alert("Failed to cancel order.");
    }
  };

  // Generate Label
  const generateLabel = async (shipmentId) => {
    try {
      const response = await axios.post("/api/orders", {
        action: "generate-label",
        data: { shipmentId },
      });
      const labelUrl = response.data.label_url;
      window.open(labelUrl, "_blank");
    } catch (error) {
      console.error("Error generating label:", error);
      alert("Failed to generate label.");
    }
  };

  return (
    <div style={{ padding: "20px" }}>
      <h1>Shipping Management</h1>

      <div style={{ marginBottom: "20px" }}>
        <h2>Create New Order</h2>
        <input
          type="text"
          placeholder="Customer Name"
          onChange={(e) => setNewOrder({ ...newOrder, customer_name: e.target.value })}
        />
        <input
          type="text"
          placeholder="Address"
          onChange={(e) => setNewOrder({ ...newOrder, address: e.target.value })}
        />
        <button onClick={createShippingOrder}>Create Order</button>
      </div>

      <h2>Orders List</h2>
      {loading ? (
        <p>Loading...</p>
      ) : (
        <table border="1" cellPadding="10" cellSpacing="0">
          <thead>
            <tr>
              <th>Order ID</th>
              <th>Customer</th>
              <th>Status</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {orders.map((order) => (
              <tr key={order.id}>
                <td>{order.id}</td>
                <td>{order.customer_name}</td>
                <td>{order.status}</td>
                <td>
                  <button onClick={() => cancelOrder(order.shipment_id)}>Cancel</button>
                  <button onClick={() => generateLabel(order.shipment_id)}>Generate Label</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}
